import {request} from "@/network/request";

//登录接口---手机登录
export  function phoneLogin(url,method,data){
    return  request('http://139.155.39.14:3000'+url,method,data);
}

//登录接口---获取用户详情
export  function getUserInfo(url,data){
    return  request('http://139.155.39.14:3000'+url,'get',data);
}


//登录接口---发送验证码
export  function captchaAdd(url,method,data){
    return  request('http://139.155.39.14:3000'+url,method,data);
}

//登录接口---验证验证码
export  function captchaVerify(url,method,data){
    return  request('http://139.155.39.14:3000'+url,method,data);
}