<template>
  <div class="payController">
      <div class="left" @click="PlayMusicShow=!PlayMusicShow">
        <img :src="playlist[playCurrentIndex].al.picUrl">
        <div class="content">
              <span class="title">{{playlist[playCurrentIndex].name}}</span>
              <p>横滑可以切换下一首</p>
        </div>
      </div>
      <div class="right">
        <!--播放-->
        <svg v-show="audioImg" class="icon" aria-hidden="true" @click="playMusic">
          <use xlink:href="#icon-bofang"></use>
        </svg>
        <!--暂停-->
        <svg v-show="!audioImg" class="icon" aria-hidden="true" @click="playMusic">
          <use xlink:href="#icon-zanting"></use>
        </svg>
        <!--更多-->
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-danlieliebiao"></use>
        </svg>
      </div>
    <!--大播放器-->
    <PlayMusic v-show="PlayMusicShow" :playDetail="playlist[playCurrentIndex]" @closeMusic="showMusic"
    :playMusic="playMusic"
    ></PlayMusic>
    <audio ref="audio" :src="`https://music.163.com/song/media/outer/url?id=${playlist[playCurrentIndex].id}.mp3`"></audio>
  </div>
</template>

<script>
import PlayMusic from '@/components/PlayMusic';
import {useStore,mapState,mapMutations,} from 'vuex'
import {reactive, computed, onMounted, watch, toRefs, ref,onUpdated} from "vue";

export default {
  name: "Pay-controller",
  components:{
    PlayMusic
  },
  setup(){
    //创建一个变量用于控制图片显示
    let imgExistence = ref(false);
    //创建一个变量用于获取audio
    const audio = ref(null);
    //创建一个变量用于显示切换播放案例图标,默认为true播放按钮显示
    const audioImg = ref(true);
    //创建一个变量用于显示大播放器
    const PlayMusicShow = ref(false);
    //实例化vuex
    const store = useStore();

    //创建一个方法用于接收子组件传递的数据，用来关闭大播放器
    const showMusic = function (data){
      PlayMusicShow.value =data
    }

    //创建一个空响应式对象
    const VuexData = reactive({
      playlist:[],
      playCurrentIndex:''
    });
    //创建播放音乐的方法
    const playMusic = function (){
      //判断当前是否正在播放音乐如果是再次点击就是关闭，否则就是播放
      if(store.state.playStatus){
        store.commit('setPlayStatus',false);
        audioImg.value=true;
        audio.value.pause();
      }else{
        //如果没有播放就开始播放,默认为没有播放
        store.commit('setPlayStatus',true);
        audio.value.play();
        //开始播放按钮设置为暂停
        audioImg.value=false;
      }

      //创建一个监听，监听vuex里数据变化，更改播放按钮
      watch(()=>store.state.playStatus,(newA,oldA)=>{
        audioImg.value = !store.state.playStatus;
        console.log('变化了')
      },{
        deep:true
      })

    }

    //监听vuex里的数据，如果出现立马赋值
    watch(()=>store.state,(newA,oldA)=>{
      VuexData.playlist = store.state.playlist;
      VuexData.playCurrentIndex = store.state.playCurrentIndex;
      //如果从vuex里拿到的数据不为空就显示图片
      if(VuexData.playlist.length!==0){
        imgExistence.value =true;
        console.log('数据不为空')
      }
    },{deep:true});

    onMounted(()=>{
      //调用方法获取歌词
      store.dispatch('reqLyric',store.state.playlist[store.state.playCurrentIndex].id);
    });

    onUpdated(()=>{
      //调用方法获取歌词
      store.dispatch('reqLyric',store.state.playlist[store.state.playCurrentIndex].id);
    })

    return {
      playlist:computed(()=>store.state.playlist),
      playCurrentIndex:computed(()=>store.state.playCurrentIndex),
      imgExistence,audio,playMusic,audioImg,PlayMusicShow,showMusic
    }
  }
}
</script>

<style scoped lang="less">
.payController{
  width: 7.5rem;
  height: 1.2rem;
  position: fixed;
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #cccccc;
  padding: 0.1rem;
  background: #FFFFFF;
  .left{
      display: flex;
    img{
      width: 0.8rem;
      height: 0.8rem;
      border-radius: 50%;
      overflow: hidden;
    }
    .content{
      padding:0 0.2rem;
      .title{

        color: #999999;
      }
    }
  }
  .right{
    .icon{
      width: 0.6rem;
      height: 0.6rem;
     margin: 0 0.2rem;
    }
  }
}

</style>