<template>
  <div class="swiper-container" id="swiperIndex">
    <swiper class="swiper" :options="swiper_options"
            :modules="modules" :pagination="swiper_options.pagination"
            :loop="swiper_options.loop"
            :autoplay="swiper_options.autoplay" @swiper="setControlledSwiper">
      <swiper-slide v-for="(item,index) in res" :key="index">
        <img :src="item.pic" alt="">
      </swiper-slide>
      <div class="swiper-pagination"></div>
      <div class="swiper-button-prev"></div>
      <div class="swiper-button-next" @click="next">
      </div>
    </swiper>
  </div>
</template>

<script>
import {getBannerImg} from '@/network/getBanner';
import {computed, onMounted, reactive, toRefs} from 'vue'
import SwiperClass, { Pagination,Autoplay,EffectCoverflow,Navigation } from 'swiper'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css'
import 'swiper/css/pagination'
import 'swiper/css/navigation'
SwiperClass.use([Autoplay, Pagination, EffectCoverflow,Navigation]);


export default {
  components: {
    Swiper,SwiperSlide
  },
  name: "SwiperCom",

  setup(){
    //创建图片变量
    let imgData = reactive({
      res:[]
    });

    //图片
    let img = reactive([require('@/assets/img/1.jpg'),require('@/assets/img/2.jpg'),require('@/assets/img/3.jpg')]);

    //初始化
    const data = reactive({
      controlledSwiper: null
    })

    const setControlledSwiper = function (swiper){
      data.controlledSwiper = swiper
    }

    //swiper配置
    let swiper_options = reactive({
      autoplay: {
        disableOnInteraction: false, // 鼠标滑动后继续自动播放
        delay: 4000, //4秒切换一次
      },
      loop: true,
      navigation: {
        nextElRef: ".swiper-button-next",
        prevElRef: ".swiper-button-prev",
      },
      pagination: {
        el: '.swiper-pagination',
        clickable: true,
      },
    });

    const onSwiper = (swiper) => {
      console.log('进入页面触发',swiper);
    };
    const onSlideChange = () => {
      console.log('slide change');
    };

    //点击下一页
    const next = function (){
        data.controlledSwiper.slideNext();
    }
    //创建获取轮播图的方法，到onMounted里调用
    const getBan = async function (){
        const res = await  getBannerImg('/banner','get',{
          type:'2'
        });
       imgData.res =[...res.data.banners];
      console.log(imgData.res)
    }

    onMounted( ()=>{
      getBan();

    });

    return{
      modules: [Pagination,Navigation],onSwiper,onSlideChange,swiper_options,setControlledSwiper,next,img,...toRefs(imgData)
    }
  },

}
</script>

<style scoped lang="less">

.swiper-container{
  width: 7.1rem;
  margin: 0 auto;
  border-radius: 0.3rem;
  overflow: hidden;
}
.swiper-slide{
  width: 7.1rem;
  height: 4rem;
  border-radius: 0.3rem;
  overflow: hidden;
  margin: 0 auto;
  img{
    width: 7.1rem;
    height: 3.75rem;
    border-radius: 0.3rem;
    overflow: hidden;
    //object-fit: cover;
  }

}
.swiper-pagination{
  margin-bottom: 0.3rem;
}
/deep/.swiper-pagination-bullet-active{
  background: #f04545;
}

</style>