<template>
  <div>
    <router-view/>
    <!--全局底部播放组件-->
    <PayController v-if="PayController"></PayController>
  </div>
</template>

<script>
import {reactive, ref, watch,onMounted} from 'vue';
import {useStore} from 'vuex';
  import PayController from '@/components/Pay-controller';
import Login from "@/views/Login";
  export default {
    components:{
      PayController
    },
    setup(){
      const store = useStore();
      //创建一个变量用于控制全局底部播放组件显示
      let PayController = ref(false);

      //监听vuex里的数据，如果出现立马赋值
      watch(()=>store.state,(newA,oldA)=>{
        //如果从vuex里拿到的数据不为空就显示图片
        if(store.state.playlist.length!==0){
          PayController.value =true;
        }else{
          PayController.value =false;
        }
      },{deep:true});

      //数据挂载前，把数据取出来
      onMounted(()=>{
        let userData = JSON.parse(localStorage.userData);
        //取出数据后重新给VUEX存进去
        store.commit('setUserDetail',userData)
        if(userData.isLogin){
          store.commit('setLogin',true);
        }

        console.log('用户数据',userData);
      });

      return{
        PayController
      }
    }
  }

</script>

