<template>
  <div class="musicList">
      <div class="musictitle">
        <span class="left">发现好歌单</span>
        <span class="right">查看更多</span>
      </div>
      <div class="mlist">
        <swiper class="swiper-content" @swiper="onSwiper"
                @slideChange="onSlideChange"
                :modules="modules" :pagination="swiper_options.pagination"
                slides-per-view="auto">

          <swiper-slide class="slide" v-for="(item,index) in  musicList" :key="item.id">
            <router-link :to="{path:'/listmusic',query:{id:item.id}}">
            <img :src="item.picUrl" alt="item.name">
            <span>{{ item.name }}</span>
            <p>{{filterCount(item.playCount)}}</p>
            </router-link>
          </swiper-slide>
        </swiper>
      </div>
  </div>
</template>

<script>
import {useStore} from 'vuex'
import SwiperClass, { Pagination,Autoplay,EffectCoverflow,Navigation } from 'swiper'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css'
import 'swiper/css/pagination'
import {onMounted, reactive, toRefs,computed} from 'vue'
import {getNewMusic} from "@/network/getNewMusic";
SwiperClass.use([Autoplay, Pagination, EffectCoverflow,Navigation]);
export default {
  name: "FindMusic",
  components: {
    Swiper,SwiperSlide
  },
  setup(props,{commit}){
    const store = useStore();
    let swiper_options = reactive({
      autoplay: {
        disableOnInteraction: false, // 鼠标滑动后继续自动播放
        delay: 4000, //4秒切换一次
      },
      speed: 500, //切换过渡速度
      loop: true,
      slidesPerView: "auto", //解决最后一张切换到第一张中间的空白
      centeredSlides: true, //设置slide居中
      spaceBetween: 20,
      coverflowEffect: {
        // rotate: 0, //slide做3d旋转时Y轴的旋转角度。默认50。
        stretch: 50, //每个slide之间的拉伸值（距离），越大slide靠得越紧。 默认0。
        depth: 100, //slide的位置深度。值越大z轴距离越远，看起来越小。 默认100。
        modifier: 1, //depth和rotate和stretch的倍率，相当于            depth*modifier、rotate*modifier、stretch*modifier，值越大这三个参数的效果越明显。默认1。
        // slideShadows: false, //开启slide阴影。默认 true。
      },
      navigation: {
        nextElRef: ".swiper-button-next",
        prevElRef: ".swiper-button-prev",
      },
      pagination: {
        el: '.swiper-pagination',
        clickable: true,
      },
    });
    //获取每日推荐歌单
    const newmusic = reactive({
        musicList:[]
    })
    //计算属性,操作
    const filterCount = function (data){
      //创建一个变量用于显示数字
      let num = ''
      if(data>10000){
          num = data/10000;
      }
      return parseInt(num)+'w';
    }

    //获取每日推荐歌单的方法
    const getmusic = async function (){
      const data  = await getNewMusic('/personalized','get',{
        limit:8
      });
      newmusic.musicList = [...data.data.result];
    }

    onMounted(()=>{
      //发送请求获取每日歌单
      getmusic();
    });


    return {swiper_options,modules: [Pagination],...toRefs(newmusic),filterCount}

  }
}
</script>

<style scoped lang="less">
.musicList{
  margin-top: 0.34rem;
    .musictitle{
      display: flex;
      justify-content: space-between;
      margin:0 0.2rem;
      .left{
        font-weight: 600;
        font-size: 0.33rem;
      }
      .right{
        border: 0.01rem solid #d6d3d3;
        border-radius: 0.5rem;
        padding: 0.05rem 0.1rem;
      }
    }
    .swiper-content{
      margin-top: 0.3rem;
      //padding: 0.05rem;
      .slide{
        position: relative;
        margin: 0 0.2rem;
        img{
          width: 2.34rem;
          border-radius: 0.28rem;
        }
        span{
          padding: 0 0.1rem;
          width: 100%;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        p{
          position: absolute;
          top: 0;
          right: 0;
          color: #FFFFFF;
        }
      }
      .slide::before{
        content: '';
        display: block;
        width: 1rem;
        height: 1rem;
        position: absolute;
        top: 0.05rem;
        right: 0.05rem;
        background: url("@/assets/img/4.png") no-repeat;
      }
    }
}
.swiper-wrapper{
  .swiper-slide{
    width: 30%;
  }
}
</style>