<!--大音乐播放界面-->
<template>
  <div class="playMusic">

      <div class="playTop">
        <div class="back"  @click="backButton">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-left"></use>
          </svg>
        </div>
        <div class="center">
         <div class="title">{{ playDetail.name }}</div>
          <div class="anthor">{{ playDetail.al.name }}</div>
        </div>
        <div class="share">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-fenxiang"></use>
          </svg>
        </div>
      </div>
        <div v-show="!lyric" class="playContent" @click="playMusic">
          <img :class="{one:true,oneactive:animato}" src="@/assets/playMusic/3.png" alt="">
          <img class="two" src="@/assets/playMusic/1.png">
          <div :class="{boderImg:true,boderImgActive:animato}" :style="{backgroundImage:`url(${playDetail.al.picUrl})`}"></div>
          <div v-show="!animato" class="playBtn">
            <img src="@/assets/playMusic/play.png" alt="">
          </div>
        </div>
        <div v-show="lyric" class="playLyric">

        </div>
        <div class="progress"></div>
        <div class="playFoot">
          <!--循环-->
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-repeat2"></use>
          </svg>
          <!--上一首-->
          <svg class="icon" aria-hidden="true" @click="NextSong">
            <use xlink:href="#icon-shangyishoushangyige"></use>
          </svg>
          <!--播放-->
          <div class="playBtnF">
            <!--播放-->
            <svg v-if="!animato" class="icon" aria-hidden="true" @click="playMusic">
              <use xlink:href="#icon-bofang"></use>
            </svg>
            <!--暂停-->
            <svg v-else class="icon" aria-hidden="true" @click="playMusic">
              <use xlink:href="#icon-zanting"></use>
            </svg>
          </div>
        <!-- 下一首 -->
          <svg class="icon" aria-hidden="true" @click="NextSong(1)">
            <use xlink:href="#icon-xiayigexiayishou"></use>
          </svg>
        <!--列表-->
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-fenlei"></use>
          </svg>
        </div>
     <div class="bg" :style="{backgroundImage:`url(${playDetail.al.picUrl})`}"></div> 
  </div>
</template>

<script>
import {getLyric} from "@/network/getLyric";
import {useStore} from 'vuex';
import {ref, watch, onMounted, reactive, toRefs} from 'vue';
export default {
  name: "playMusic",
  props:{
    playDetail:{
    },
    playMusic:{

    }

  },

  setup(props,{emit}){
    //创建一个值用于绑定和显示动画效果
    let animato = ref(false);
    //创建一个变量用于判断是否显示歌词控件
    let lyric = ref(false);

    //实例化vueX
    const store = useStore();

    //创建一个监听，监听播放按钮的变化
    watch(()=>store.state.playStatus,(newA,oldA)=>{
      animato.value = store.state.playStatus;
    },{
      deep:true
    });

    //当点击了返回按钮
    const backButton = function (){
        emit('closeMusic',false);
       console.log('触发了返回按钮')
    }
    //当点击了播放按钮
    const play = function (){
      animato.value = !animato.value;
      console.log('点击了播放按钮')
    }

    //创建一个响应式对象显示歌词
    let gcData = reactive({
        gc:store.state.lyric
    })

    //创建一个监听，监听到数据变化赋值给歌词
    watch(()=>store.state.lyric,(newA,oldA)=>{
        gcData.gc = store.state.lyric;

      console.log('playMusic里的数据',gcData);
    },{
      deep:true
    });

    //创建一个切换上一首下一首的方法,传一个数据过来如果为1就是下一首，不传就是上一首
    const NextSong = function (data){
      console.log(data);
      if(data===1){
        let num = store.state.playCurrentIndex;
        store.commit('setPlayIndex',num+1);
        props.playMusic();
        console.log('修改后索引',store.state.playCurrentIndex);
      }else{
        let num = store.state.playCurrentIndex;
        store.commit('setPlayIndex',num-1);
        props.playMusic();
      }

    }

    return{
      backButton,play,animato,lyric,...toRefs(gcData),NextSong
    }
  }
}
</script>

<style scoped lang="less">

//定义图片旋转
@keyframes xuan {
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }

}

.playMusic{
  position:fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #FFFFFF;
  .playTop{
    position: absolute;
    width: 7.5rem;
    height: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.4rem;
    z-index: 5;
    color: #FFFFFF;
    .icon{
      fill: #FFFFFF;
    }
  }
  .playContent{
    display:flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1.5rem;
    position: relative;
    .playBtn{
      margin-top: -3.5rem;
      width: 2rem;
      height: auto;
      z-index: 15;
      img{
        width: 100%;
        height: auto;
      }

    }
    .one{
       width: 2rem;
       height: auto;
       margin-left: 1rem;
      z-index: 1;
      transform-origin: 0.1rem 0;
      transform: rotate(-24deg);
      transition: all 2s linear;
    }
    //设置读取头移动
    .oneactive{
      transform: rotate(0deg);
    }
    .two{
      margin-top: -1.2rem;
      width: 5rem;
      height: auto;
    }
    .boderImg{
      position: absolute;
      top: 2.9rem;
      left: 2.15rem;
      width: 3.2rem;
      height: 3.2rem;
      border-radius: 50%;
      background-position: center;
      background-size: cover;
      //animation: xuan 25s linear infinite;
    }
    //设置旋转
    .boderImgActive{
      animation: xuan 25s linear infinite;
    }

  }
  .playLyric{
    position: absolute;
    width: 7.5rem;
    height: 7.5rem;
    left: 0;
    top: 1.5rem;
    padding: 0 0.3rem;
    color: #FFFFFF;
  }
  .playFoot{
    height: 1rem;
    width: 100%;
    position: absolute;
    bottom: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 0.4rem;
    .icon{
      fill: #FFFFFF;
      width: 0.7rem;
      height: 0.7rem;
    }
  }
  .bg{
    position:absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-size: auto 100%;
    background-position: center;
    filter: blur(80px);
    z-index: -1;
  }
}

</style>