import { createStore } from 'vuex'
import {getLyric} from "@/network/getLyric";
import {phoneLogin,getUserInfo} from '@/network/login';

export default createStore({
  state: {
    playlist:[],
    //当前音乐索引
    playCurrentIndex:0,
    //当前是否在播放音乐，默认没有播放
    playStatus:false,
    //歌词id
    lyric:{},
    //是否登录
    user:{
      isLogin:false,
      account:{},
      Detail:{}
    }
  },
  getters: {
  },
  mutations: {
    //设置播放列表
    setPlaylist:function (state,value){
      state.playlist = value;
    },
    //设置当前是否播放音乐
    setPlayStatus:function (state,value){
      state.playStatus = value;
    },
    //设置当前是否播放音乐的索引
    setPlayIndex:function (state,value){
      state.playCurrentIndex = value;
    },
    setLyric:function (state,value){
      state.lyric = value;
    },
    //把登录状态设置为true
      setLogin:function (state,value){
        state.user.isLogin = value;
      },
    //设置用户信息
    setUser:function (state,value){
        state.user.account = value;
    },
    //设置用户信息
     setUserDetail:function (state,value){
          state.user.Detail = value;
      }

  },
  actions: {
      //异步请求歌词
      reqLyric: async function(content,payload){
       let result =  await getLyric('/lyric','get',{
         id:payload
       });
        content.commit('setLyric',result.data.lrc.lyric);
        console.log('异步请求',result.data);
      },
      //异步登录
      login:async function(content,payload){
          let result = await phoneLogin('/login/cellphone','post',payload);
          console.log('异步请求的结果',result);
          if(result.data.code===200){
              content.commit('setLogin',true);
              content.commit('setUser',result.data.account)
              //获取用户详细信息
              let userDetail = await getUserInfo('/user/detail',{
                  uid:result.data.account.id
              });
              //把数据存入state里
              content.commit('setUserDetail',userDetail);
              //存入localstorage里
              localStorage.userData = JSON.stringify(content.state.user)
              console.log('用户详细信息',userDetail)
          }
          return result
      }


  },
  modules: {
  }
})
